import { dateFormatFun } from '@/assets/js/common'
import that from '@/main'
import { validateInfo } from '@/network/networds'

export var validatorRule = (rule, value, callback) => {
  let fieldtxt
  if (!value) {
    return callback()
  }
  console.log(rule)
  const singleUser = JSON.parse(window.sessionStorage.getItem('singleUser'))
  if (singleUser) {
    if (singleUser.contact == value && rule.field == 'contact') {
      return callback()
    } else if (singleUser.qq == value && rule.field == 'qq') {
      return callback()
    } else if (singleUser.weixin == value && rule.field == 'weixin') {
      return callback()
    }
  }

  if (rule.field == 'contact') {
    fieldtxt = '电话'
    console.log('1')
    if (!(/^((13[0-9])|(17[0-1,6-8])|(15[^4,\\D])|(18[0-9]))\d{8}$/).test(value)) {
      console('2')
      return callback(new Error('电话格式不正确'))
    }
  } else if (rule.field == 'qq') {
    fieldtxt = 'QQ'
    if (!(/^[1-9]\d{4,9}$/).test(value)) {
      return callback(new Error('QQ格式不正确'))
    }
  } else if (rule.field == 'weixin') {
    if (!(/^[a-zA-Z][-_a-zA-Z0-9]{5,19}$/).test(value)) {
      return callback(new Error('微信格式不正确'))
    }
  }
  const config = {
    keyword: value,
    field: rule.field
  }
  validateInfo(config).then(res => {
    if (res.data.code == 403) {
      const content = res.data.content
      const time = dateFormatFun(content.posttime, 2)
      const text = `
        ${fieldtxt}为<span style="color:red">${value}</span>的用户已存在<br>
        客户名称为${content.nickname}<br>
        录入时间为${time}<br>
        请勿重复提交
        `
      that.$confirm(text, '提示', {
        confirmButtonText: false,
        cancelButtonText: false,
        dangerouslyUseHTMLString: true,
        type: 'warning'
      })
      return callback(new Error('该' + fieldtxt + '已存在'))
    } else {
      return callback()
    }
  })
}
